.homepage {
  text-align: center;
  margin: 0 30%;
  padding: 30px;
  display: flex;
  flex-direction: column;
}

@keyframes title {
  0% {
    color: rgb(47, 134, 47);
  }
  25% {
    color: rgb(171, 72, 155);
  }
  50% {
    color: rgb(45, 45, 163);
  }
  100% {
    color: var(--black);
  }
}

.intro-name {
  font-size: 50px;
  animation-name: title;
  animation-duration: 8s;
  animation-iteration-count: infinite;
}

@media screen and (min-width: 700px) {
  /* .homepage {
    padding-left: 10%;
  } */

  .home-intro {
    font-size: 25px;
  }

  .intro-name {
    font-size: 80px;
  }
}
